import React, { Component } from 'react';

class About extends Component {
    render() {
        return (
            <div className="aboutme">
                <h2>Who am I ?</h2>
                <p> I am a Software Developer from Bangalore India, having more than 2 years of professional experience. I love Web Development feel passionate about it and always try to keep myself moving with the same pace Web is growing (<i>and everytime i fail badly</i>).</p>
              <p> Most of my Development experience revolve around Web Applications but i like web scrapping and text parsing. 
          In my free time i always try to learn new things , get inspired by them and implement those principles in existing applications . </p>
         <p>When i am not working i will be found either on a Cricket ground or somewhere admiring my city.</p>
         </div>
        );
    }
}

export default About;



