import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import mugshot from '../../Images/a.jpg'

class Sidebar extends Component {
    constructor(){
        super();
        this.state = {
            selectedTab : 0,
            isFull : true
        }
    }

    handleSelectedTab(tab){
        this.setState({selectedTab: tab});
    }

    toggleSidebar(){
        this.setState({isFull: !this.state.isFull})
    }

    render() {
        return (
            <div className="inline-block sidebar" style={{flex: this.state.isFull ? '3': '1'}}>
                <div className="profile">
                    <div className="mugshot">
                        <img style={{width:'100%',height:'100%',verticalAlign:"middle"}} src={mugshot} alt="pic" onClick={this.toggleSidebar.bind(this)} />
                    </div>
                    <h3 style={{margin:0,fontSize:'1.4em'}}>Ankit Kumar</h3>
                    <span className="capsule">Software Developer</span>
                </div>
                <ul>
                    <Link to={window.routesUrl.home}>
                        <li style={{background: this.state.selectedTab == 0 ?'#E91E63':''}} onClick={this.handleSelectedTab.bind(this,0)}>
                                About
                        </li>
                    </Link>
                    <Link to={window.routesUrl.skills}>
                        <li style={{background: this.state.selectedTab == 1 ?'#E91E63':''}} onClick={this.handleSelectedTab.bind(this,1)}>
                                Skills
                        </li>
                    </Link>
                    <Link to={window.routesUrl.showcase}>
                        <li style={{background: this.state.selectedTab == 2 ?'#E91E63':''}} onClick={this.handleSelectedTab.bind(this,2)}>
                                Showcase
                        </li>
                    </Link>
                    <Link to={window.routesUrl.contact}>
                        <li style={{background: this.state.selectedTab == 3 ?'#E91E63':''}} onClick={this.handleSelectedTab.bind(this,3)}>
                                Contact
                        </li>
                    </Link>
                </ul>                
            </div>
        );
    }
}
export default Sidebar;



