import React, { Component } from 'react';

class ProCard extends Component {
    render() {
        var desc = this.props.isHovered ? this.props.desc_full : this.props.desc_small;
        return (
            <div className="card" 
                  onMouseOver={this.props.handleHover.bind(this,this.props.projId,"in")}
                  onMouseOut={this.props.handleHover.bind(this,this.props.projId,"out")}
                  style={{backgroundImage: 'url('+this.props.img_src+')' }}
                 >
                {/*<img src={this.props.img_src} />*/}
                <p className="card-desc" dangerouslySetInnerHTML={{__html:desc}}>
                </p>	
            </div>
        );
    }
}

export default ProCard;



