import React, { Component } from 'react';

import Data from '../../Services/Data'

class Contact extends Component {
     sendEmail(ev){
        ev.preventDefault();
        var body = 'email='+ev.target.email.value+'&msg='+
                         encodeURIComponent(ev.target.msg.value);

        Data.getData('https://mailatnodemailer.herokuapp.com/api/hello','POST',body)
            .then((res) => {
                ev.target.email.value = "";
                ev.target.msg.value = "";
                alert('Email sent');
            })
        return false;
    }
    render() {
        return (
            <div>
                <h2>Contact</h2>
                <div>
                    <form style={{display:"inline-block",position:"relative",bottom:"3px"}}
                              action="#" onSubmit={this.sendEmail.bind(this)}
                        >
                          <input  name="email" type="email" placeholder='Enter your email' />
                          <input  name="msg" type="text" placeholder='Say hi to Ankit'/>
                          <input type="submit"/>
                        </form> 
                </div>
            </div>
        );
    }
}

export default Contact;


