import React, { Component } from 'react';
import ProCard from './ProCard'

import logo from '../../Images/a.jpg';
import oyiShot  from '../../Images/projects/oyi/oyi2.png';
import hospitalShot  from '../../Images/projects/hospital/1.png';
import maasterjiShot  from '../../Images/projects/maasterji/1.png';
import techfestShot  from '../../Images/projects/techfest/1.png';
import moviesShot  from '../../Images/projects/movies/1.png';
import readerShot  from '../../Images/projects/reader/1.png';
import newsShot  from '../../Images/projects/news/1.png';
import usermanagShot  from '../../Images/projects/usermanag/1.png';
import mailerShot  from '../../Images/projects/mailer/1.png';

import Card from '../Resuable/Card.js';
import img1 from '../../Images/1.png';

class Showcase extends Component {
    constructor(){
      super();
      this.state = {
        cardsData:[
          {id:0 ,img_src:oyiShot,desc_small:"A Dashboard from a Cab Company Oyicab&copy;",desc_full:"A Dashboard from a Cab Company Oyicab&copy; where you can see all the stats and Book an Cab as well",isHovered:false},
          {id:1 ,img_src:hospitalShot,desc_small:"A Hospital Management Software",desc_full:"A Hospital Management System based on J2EE",isHovered:false},
   	      {id:2 ,img_src:maasterjiShot,desc_small:"A Online Platform for leanring",desc_full:"A online collabrative learning platform where teachers and students can meet.",isHovered:false},
          {id:5 ,img_src:readerShot,desc_small:"Simple dashboard with auto form fill",desc_full:"A dashboard with autofilled forms based on imported text file using html5 FileReader apis and Regex.",isHovered:false},
          {id:7 ,img_src:usermanagShot,desc_small:"A User Management app",desc_full:"A User Management where you can add-search users ,see their details and change them .",isHovered:false},
          {id:8 ,img_src:mailerShot,desc_small:"A Test organizing App",desc_full:"A Test organizing application which will authenticate users and will give random set of 10 MCQ Question(half way done)",isHovered:false},
          {id:4 ,img_src:moviesShot,desc_small:"A movie listing app",desc_full:"A movie listing app using IMDB apis.",isHovered:false},
          {id:3 ,img_src:techfestShot,desc_small:"A website for my College's Techfest",desc_full:"A informational website for my College's annual TechFest Prayog-2015",isHovered:false},
          {id:6 ,img_src:newsShot,desc_small:"A HackerNews&copy; clone",desc_full:"A HackerNews&copy; clone with some different features",isHovered:false},
        ],
      }
    }

   handleHover(id , type , ev){
      this.setState({cardsData:this.state.cardsData.map((elem) => {
   	  return  elem.id == id ? Object.assign({},elem,{isHovered: type == "in" ? true : false}):elem;	
         })
      })
   }

    render() {
        return (
            <div>
                <h2>Few of my works</h2>
                <div className="showcase-container">
                   {this.state.cardsData.map((elem,index) => {
                     return(
                         <ProCard
                            projId={elem.id}
                            handleHover={this.handleHover.bind(this)}
                            isHovered={elem.isHovered}  
                            img_src={elem.img_src} 
                            desc_small={elem.desc_small}
                            desc_full={elem.desc_full}
                          />
                      ) 
                   })}
                </div>
           </div>
        );
    }
}

export default Showcase;



