import React, { Component } from 'react';
import { BrowserRouter as Router , Route } from 'react-router-dom';

import About from '../About'
import Skills from '../Skills'
import Showcase from '../Showcase'
import Contact from '../Contact'

import Data from '../../Services/Data'

class Mainarea extends Component {
    sendEmail(ev){
        ev.preventDefault();
        var body = 'email='+ev.target.email.value+'&msg='+
                         encodeURIComponent(ev.target.msg.value);

        Data.getData('https://mailatnodemailer.herokuapp.com/api/hello','POST',body)
            .then((res) => {
                ev.target.email.value = "";
                ev.target.msg.value = "";
                alert('Email sent');
            })
        return false;
    }
    render() {
        return (
            <div className="inline-block mainarea">
                <div className="section">
                    <Route exact path={window.routesUrl.home} component={About} />
                    <Route path={window.routesUrl.skills} component={Skills} />
                    <Route path={window.routesUrl.showcase} component={Showcase} />
                    <Route path={window.routesUrl.contact} component={Contact} />
                </div>
                <div className="footer">
                    <b style={{marginLeft:"20px",marginRight:"10px"}}>Contact :</b>
                    <span className="contact-form">
                        <form style={{display:"inline-block",position:"relative",bottom:"3px"}}
                              action="#" onSubmit={this.sendEmail.bind(this)}
                        >
                          <input  name="email" type="email" placeholder='Enter your email' />
                          <input  name="msg" type="text" placeholder='Say hi to Ankit'/>
                          <input type="submit"/>
                        </form>    
                    </span> 
                    <span className="social">
                        <a target="_blank" href="https://www.linkedin.com/in/iankit3" className="fa fa-linkedin"></a>
                        <a target="_blank" href="https://twitter.com/iankit3" className="fa fa-twitter"></a>
                        <a target="_blank" href="https://www.github.com/iankit3" className="fa fa-github"></a>
                    </span>
                </div>
            </div>
        );
    }
}

export default Mainarea;


